import styled from "@emotion/styled";

export const Container = styled.div`
  width: 400px;
  background-color: #f0f0f0;
  border-radius: 5px;
  padding: 30px;
  margin-left:auto;
  margin-right: auto;
  margin-top:30px;
`;

export const Title = styled.h1`
   display: block;
   font-size: 26px;
   font-weight: 800;
   text-align: center;
   text-transform: uppercase;
   margin-bottom: 10px;
`;

export const SectionTitle = styled.h2`
   display: block;
   font-size: 20px;
   font-weight: 800;
   text-align: center;
  text-transform: uppercase;
  margin-top:30px;
   margin-bottom:10px;
`;